import React, { Component } from 'react';
import ChildComponent from '../components/child-comp';

class ParentPage extends Component {

  constructor(props){
    super(props);
    this.state = {
      username : 'Poorna',
      message :''
    }
  }

  onReceiveValue=(value)=>{
     console.log(value)
     this.setState({
         message:value
     })
  }

  render() {
    return (
      <div>
        <h1>This is a Parent Componenet</h1>
        <ChildComponent name={this.state.username} {...this.state} onLoadData={this.onReceiveValue}></ChildComponent>
      </div>
    );
  }
}

export default ParentPage; 